 /* Zooming */
  
 .zoom-2 {
    zoom: 0.3 !important;
    -ms-zoom: 0.3 !important;
    -webkit-zoom: 0.3 !important;
    -moz-transform:  scale(0.3 ,0.3) !important;
    /*-moz-transform-origin: center top;*/
  }
  
  .zoom-1 {
    zoom: 0.4 !important;
    -ms-zoom: 0.4 !important;
    -webkit-zoom: 0.4 !important;
    -moz-transform:  scale(0.4 ,0.4) !important;
    /*-moz-transform-origin: center top;*/
  }
  
  .zoom1 {
    zoom: 0.75 !important;
    -ms-zoom: 0.75 !important;
